@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&family=Roboto:ital,wght@0,500;0,700;0,900;1,400&display=swap');

:root {
    --padding: 15vh;
    --nav: 80px;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Lato', sans-serif;
}

*::selection {
    background-color: rgb(255, 200, 0);
}

body {
    overflow-x: hidden;
}


/*! NAV */

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    margin: 0.5rem auto;
}

.nav_logo {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.nav_logo img {
    width: 75px;
    height: 75px;
    aspect-ratio: 1/1;
    cursor: pointer;
    user-select: none;
}

.nav_logo p {
    font-size: 1.375rem;
    font-weight: 700;
    margin: 0 1.5rem;
    cursor: pointer;
    letter-spacing: 0.5px;
    word-spacing: 2px;
}

.nav_links span {
    font-size: 1.25rem;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    margin: 0 1.25rem;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 0.2px;
}

.nav_links {
    display: flex;
    justify-content: space-around;
    align-items: center;
}


/*! HEADER */

header {
    background: white;
    clip-path: inset(0 0 4px 0);
}

.header_heading {
    font-family: 'Lato', sans-serif;
    margin: 0;
    text-transform: uppercase;
    text-align: center;
    z-index: 5;
    font-size: 5.5rem;
    line-height: 0.86;
    font-weight: 900;
    letter-spacing: 1rem;
    user-select: none;
}

.header_image {
    margin-top: 20vh;
    user-select: none;
    height: 62.5vh;
    width: 100vw;
    object-fit: cover;
    object-position: center;
    filter: contrast(0.75);
}


/*! SERVICES */

#services {
    text-align: center;
    width: 80%;
    margin: 7vh auto;
}

#services h1 {
    font-size: 2.8em;
    font-weight: 700;
}

#services p {
    font-size: 1.35rem;
}

#services button {
    margin-top: 2.25rem;
    font-family: 'Roboto';
    border: 2px solid #000;
    color: #fff;
    background-color: #000;
    border-radius: 8px;
    outline: none;
    padding: 16px;
    font-size: 1.2rem;
    width: 200px;
}

#services .contact-button {
    background-color: transparent;
    color: #000;
    margin-right: 1.5rem;
}

.services-examples-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin: 10vh auto;
}

.example-service {
    text-align: left;
    min-width: 450px;
    border-radius: 8px;
    border: 2px solid rgb(150, 150, 150);
}

.popular-choice {
    font-family: 'Roboto';
    border-radius: 6px 0px;
    background: rgba(0, 0, 0, 0.25);
    color: #000;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 8px 0 0 0;
    padding: 5px;
}

.example-service .service-image {
    position: relative;
    border-radius: 8px 8px 0 0;
    height: 450px;
    width: 100%;
    background-image: url('https://images.unsplash.com/photo-1636589150123-6d57c10527ce?q=80&w=1974&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
}

.example-service .second-example {
    background-image: url('https://images.unsplash.com/photo-1512439408685-2e399291a4e6?q=80&w=1995&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
}

.example-service .third-example {
    background-image: url('https://images.unsplash.com/photo-1598346653523-10a11538b971?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
}

.service-info {
    padding: 14px;
}

#services .service-info p {
    font-family: 'Roboto';
    font-size: 1.1rem;
}

#services .service-info .service-info-price {
    font-family: 'Lato';
    margin-top: 0.3rem;
    font-size: 1.325rem;
    font-weight: 700;
}

@layer headerScroll {
    header {
        background-color: white;
    }

    .header_heading {
        position: sticky;
        top: calc(var(--padding) + var(--nav));
        mix-blend-mode: difference;
        color: white;
        transition: mix-blend-mode 0.5s ease;
    }

    .scrolled {
        mix-blend-mode: revert;
    }

    .header_image {
        margin-top: calc(var(--padding) + var(--nav) var(--nav));
    }
}